import {
  Badge,
  Box,
  Checkbox,
  Collapse,
  Flex,
  Grid,
  GridItem,
  Icon,
  LightMode,
  Popover,
  PopoverArrow,
  PopoverBody,
  PopoverContent,
  PopoverHeader,
  PopoverTrigger,
  Progress,
  Stat,
  StatLabel,
  StatNumber,
  useColorModeValue,
} from "@chakra-ui/react";
import React from "react";
import { GraphContext, UserContext } from "../Contexts";
const Footer = () => {
  const { logged, user } = React.useContext(UserContext);
  const { promedio, creditos, toggleCheckbox } = React.useContext(GraphContext);

  return (
    <Collapse in={logged} key={user.carrera?.id} position="relative">
      <Flex
        alignItems="center"
        bg={useColorModeValue("headerbg", "headerbgdark")}
      >
        <Box>
          <Stat p="0.4em" color="white" size="sm">
            <StatLabel>Crditos</StatLabel>
            <StatNumber>
              {creditos.reduce((acc, c) => {
                acc += c.checkbox ? 0 : c.creditos;
                return acc;
              }, 0) +
                " de " +
                user.carrera?.creditos.total}
            </StatNumber>
          </Stat>
        </Box>
        <Grid
          flexGrow={1}
          mx={5}
          columns={creditos.length}
          templateColumns="repeat(10, 1fr)"
        >
          {creditos.map((c) => (
            <GridItem colSpan={c.proportion}>
              <Popover placement="top" trigger="hover">
                <LightMode>
                  <PopoverTrigger>
                    <Box>
                      {c.creditos >= c.creditosNecesarios ? (
                        <Icon boxSize={5} ml={2} viewBox="0 0 640 512">
                          <path
                            fill={c.bg}
                            d="M622.34 153.2L343.4 67.5c-15.2-4.67-31.6-4.67-46.79 0L17.66 153.2c-23.54 7.23-23.54 38.36 0 45.59l48.63 14.94c-10.67 13.19-17.23 29.28-17.88 46.9C38.78 266.15 32 276.11 32 288c0 10.78 5.68 19.85 13.86 25.65L20.33 428.53C18.11 438.52 25.71 448 35.94 448h56.11c10.24 0 17.84-9.48 15.62-19.47L82.14 313.65C90.32 307.85 96 298.78 96 288c0-11.57-6.47-21.25-15.66-26.87.76-15.02 8.44-28.3 20.69-36.72L296.6 284.5c9.06 2.78 26.44 6.25 46.79 0l278.95-85.7c23.55-7.24 23.55-38.36 0-45.6zM352.79 315.09c-28.53 8.76-52.84 3.92-65.59 0l-145.02-44.55L128 384c0 35.35 85.96 64 192 64s192-28.65 192-64l-14.18-113.47-145.03 44.56z"
                          />
                        </Icon>
                      ) : (
                        <>
                          <Badge colorScheme={c.color} variant="solid">
                            {Math.round(
                              (c.creditos / c.creditosNecesarios) * 100
                            ) || 0}
                            %
                          </Badge>
                          <Badge
                            ml={1}
                            colorScheme={"whiteAlpha"}
                            variant="solid"
                          >
                            {c.nombrecorto}
                          </Badge>
                        </>
                      )}
                      <Progress
                        hasStripe
                        height={4}
                        css={{
                          backgroundColor: c.bg,
                        }}
                        max={c.creditosNecesarios}
                        value={c.creditos}
                        colorScheme={c.color}
                      />
                    </Box>
                  </PopoverTrigger>
                </LightMode>
                <PopoverContent>
                  <PopoverArrow />
                  <PopoverHeader border="none">
                    <strong>{c.nombre}</strong>
                  </PopoverHeader>
                  <PopoverBody>
                    {c.checkbox ? (
                      <LightMode>
                        <Checkbox
                          isIndeterminate={!!!c.check}
                          isChecked={!!c.check}
                          colorScheme={c.color}
                          onClick={(e) => {
                            e.preventDefault();
                            toggleCheckbox(c.nombre);
                          }}
                        >
                          Marcar como completo
                        </Checkbox>
                      </LightMode>
                    ) : c.creditosNecesarios ? (
                      `${c.creditos} de ${c.creditosNecesarios} crditos necesarios.`
                    ) : (
                      `Tens ${c.creditos} crditos.
                      Eleg entre tesis y tpp para saber cuantos necesits.`
                    )}
                  </PopoverBody>
                </PopoverContent>
              </Popover>
            </GridItem>
          ))}
        </Grid>
        <Box>
          <Stat p="0.4em" color="white" size="sm" textAlign="right">
            <StatLabel>Promedio</StatLabel>
            <StatNumber>{promedio}</StatNumber>
          </Stat>
        </Box>
      </Flex>
    </Collapse>
  );
};

export default Footer;
